
import { defineComponent, reactive } from "vue"
import Headbar from "@/components/headbar/index.vue"
export default defineComponent({
    name: "Home",
    components: {
        Headbar
    },
    setup() {
        let list = reactive([
            { text: "创建", path: "/scene" },
            { text: "剪辑", path: "/clip" },
            { text: "片库", path: "/library" },
            { text: "社区", path: "/community" }
        ])
        const clickItem = (text: String) => {
            switch (text) {
                case "创建":
                    break;
                default:
                    break;
            }
            console.log(text);
        }
        return {
            list,
            clickItem
        }
    }
})
